import React from 'react';
import { Empty, Card } from 'antd';

// CSS
const getRarityClass = (rarity) => {
    switch (rarity.name) {
        case '':
            return 'rarity-covert';
        case '':
            return 'rarity-classified';
        case '':
            return 'rarity-milspec';
        default:
            return '';
    }
};

// 
const getWeaponTypeName = (internalName) => {
    const typeMap = {
        'csgo_type_pistol': '',
        'csgo_type_smg': '',
        'csgo_type_rifle': '',
        'csgo_type_shotgun': '',
        'csgo_type_machinegun': '',
        'csgo_type_sniperrifle': ''
    };
    return typeMap[internalName] || internalName;
};

const WeaponList = ({ items }) => {
    if (!items || items.length === 0) {
        return (
            <Empty
                description=""
                style={{ margin: '40px 0' }}
            />
        );
    }

    return (
        <div className="weapon-grid">
            {items.map((item, index) => (
                <Card
                    key={index}
                    className="weapon-card"
                    hoverable
                    cover={
                        <img
                            alt={item.name}
                            src={item.icon_url}
                            className="weapon-image"
                        />
                    }
                >
                    <div className="weapon-info">
                        <div className={`weapon-name ${getRarityClass(item.rarity)}`}>
                            {item.name}
                        </div>
                        <div className="weapon-type">
                            {getWeaponTypeName(item.type.internal_name)}
                        </div>
                        <div className="weapon-container">
                            {item.container}
                        </div>
                    </div>
                </Card>
            ))}
        </div>
    );
};

export default WeaponList; 